.card {
	width: 250px;
	height: 340px;
	border: none;
	cursor: pointer;
}

.card:hover {
	box-shadow: -1px -1px 35px -9px rgb(0,0,0, 0.5);
	transform: scale(1.01);
}
.card:active {
	box-shadow: -1px -1px 35px -9px rgb(0,0,0, 0.7);
}
.CB9vby5YRbJWpv7hRrZ0x {
	height: 360px;
}
.card-img-top:hover {
		border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.card-img-top{

	display: block;
	width: 100% !important;
	height: 174px;

}
.card-title {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 21px;
	font-size: 14px;
	text-align: center;
	text-transform: uppercase;

	color: #4F4F4F;
}
.card-price {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 35px;
	font-size: 24px;
	text-transform: uppercase;
	color: #2D9CDB;
	position: absolute;
	bottom: 0px;
	right: 20px;
}

.card-text {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 19px;
	font-size: 12px;
	color: #000000;
}