.information-block {
	margin-top: 100px;
	height:400px;
}
.information-title {
	position: absolute;
	width: 412px;
	height: 48px;
	left: 130px;
	margin-top: 15px;
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;
	letter-spacing: 0.02em;
	color: #09293C;
}

.information-text {
	position: absolute;
	width: 433px;
	height: 206px;
	left: 130px;
	padding-top: 90px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 27px;
	font-size: 15px;
	letter-spacing: 0.02em;
	color: #828282;
}

.page-numbers{
	position: absolute;
	height: 17px;
	left: 122.5px;
	display: flex;
	flex-direction: row;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	font-size: 14px;
	letter-spacing: 0.02em;
	color: #B2B2B2;
    margin-top: 340px;
}

.active {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	letter-spacing: 0.02em;
	text-decoration-line: underline;
	color: #2D9CDB;
}

.controls {
	margin:7.5px;
	cursor: pointer;
}

.controls:not(.active):hover{
	color: #2D9CDB;
	opacity: 0.3;
}

.information-image-block {
	position: absolute;
	width: 680px;
	height: 383px;
	left: 630px;
}

.information-image-text {
	position: absolute;
	bottom: 0px;
	width: 238px;
	height: 79px;
	background: rgba(45, 156, 219, 0.6);
	font-family: Elsie;
	font-style: normal;
	font-weight: normal;
	line-height: normal;
	font-size: 17px;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 20px;

}
.information-image {
	position: absolute;
    width: 680px;
    height: 383px;
    z-index: -1;
}